/* EventDetails.css */

.event-details-overlay {
  display: flex;
  justify-content: center;
  align-items: flex-end; /* Align to the bottom */
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  transition: transform 0.3s ease; /* Add a smooth transition for the transform property */
  transform: translateY(100%); /* Initially off-screen, translated down by 100% */
}

.event-details-content {
  background-color: #fefefe;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease; /* Add a smooth transition for the transform property */
  transform: translateY(100%); /* Initially off-screen, translated down by 100% */
}

.event-details-overlay.active,
.event-details-content.active {
  transform: translateY(0); /* Bring the modal to the center when active */
}

.close-button {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
  cursor: pointer;
}

.close-button:hover {
  color: black;
}



